<template>
<ion-card>      
      <ion-card-content  >
      <p class="site-text"> Voor persoonlijk advies en een passende oplossing voor uw bedrijf, 
        werkt Royal Telecom samen met professionele zakelijke adviseurs. 
        Bij de Royal Telecom Business Partners vindt u alle kennis onder één dak. 
        Zij zijn niet alleen thuis in uw provincie maar ook volledig Royal Telecom gecertificeerd op het gebied van telecom en IT. 
        Of het nu gaat om een totaaloplossing, vaste telefonie, mobiele telefonie, Zakelijk Glas of IT-Oplossingen, 
        onze zakelijke adviseurs gaan graag met u in gesprek om uw bedrijfsvoering gezamenlijk verder te optimaliseren, 
        te ondersteunen en voor u te ontzorgen.</p>
    </ion-card-content>
    <ion-card-header>
        <ion-card-title class="content_header">Zoek naar een zakelijk adviseur in uw regio </ion-card-title>   
    </ion-card-header>
  <ion-grid>
    <form @submit.prevent="onSubmit">
      <ion-col>
        <ion-item>
          <ion-label>Provincie:</ion-label>
          <ion-input
            :value="zip"
            @input="zip = $event.target.value"
            placeholder="Voer NL postcode in"
            name="zip"
          ></ion-input>
        </ion-item>
      </ion-col>
      <ion-col>
        <ion-button type="submit" color="" class="btn-zoeken" expand="block">Zoeken</ion-button>
      </ion-col>
    </form>
  </ion-grid>
</ion-card>
</template>

<script>
export default {
  name: "ZipSearch",
  data() {
    return {
      zip: ""
    };
  },
  methods: {
    onSubmit() {
      // Zip Regex
      const isValidZip = /(^\d{5}$)|(^\d{5}-\d{4}$)/.test(this.zip);
      // Test for valid zip
      if (!isValidZip) {
        this.showAlert();
        this.zip = "";
      } else {
        this.$emit("get-zip", this.zip);
        this.zip = "";
      }
    },
    showAlert() {
      return this.$ionic.alertController
        .create({
          header: "Enter Zipcode",
          message: "Please enter a valid zipcode",
          buttons: ["OK"]
        })
        .then(a => a.present());
    }
  }
};
</script>
